import { useContext, useState } from 'react';

import { AccountContext } from './Account';

function Login() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  const { authenticate } = useContext(AccountContext);

  const onSubmit = (e) => {
    e.preventDefault();
    authenticate(username, password)
      .then((data) => {
        console.log('Dataaaaaaaaaaaaaaa');
        console.log(data);
       // console.log(data.getAccessToken().getJwtToken());
        alert('login success');
       // window.location.reload();
       window.location.href = '/home';
      })
      .catch((err) => {
        console.log(err);
        alert('login failure');
      });
  };

  return (

    <div>

      <section classNameName="vh-100 gradient-custom" >
        <div classNameName="container py-5 h-100">
          <div classNameName="row d-flex justify-content-center align-items-center h-100">
            <div classNameName="col-12 col-md-8 col-lg-6 col-xl-5">
              <div classNameName="card shadow-2-strong" >
                <div classNameName="card-body p-5 text-center">

                  <form classNameName='mb-5'  onSubmit={onSubmit}>

                    <h2 classNameName="mb-5">Login</h2>
                    <p classNameName=" mb-5">Por favor, digite su usuario y clave</p>

                    <div classNameName="form-outline mb-4">
                    
                        <input
                          classNameName='form-control form-control-lg'
                          placeholder='Usuario'
                          type="text"
                          value={username}
                          onChange={(e) => setUsername(e.target.value)}
                        />
                        
                    </div> 

                    <div classNameName="form-outline mb-4"> 
                        
                        <input
                          classNameName='form-control form-control-lg'
                          placeholder='Clave'
                          type="password"
                          value={password}
                          onChange={(e) => setPassword(e.target.value)}
                        />
                        
                      
                    </div>  

                    <div className="col-12">
                      <button type="submit" classNameName="btn btn-primary btn-lg btn-block">Login</button>
                    </div> 

                  </form>


                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
 
 </div>

  );
}

export default Login;